/* General Footer Styling */
.footer {
    background-color: #212121;
    color: #fff;
    padding: 40px 20px;
    font-family: Arial, sans-serif;
}

.footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

/* Footer Section Styling */
.footer-section {
    flex: 1;
    margin: 0 20px;
    min-width: 280px;
}

.footer-section h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #fff;
    display: inline-block;
    padding-bottom: 0.5rem;
}

.footer-section p {
    line-height: 1.6;
    margin-bottom: 1rem;
    font-size: 0.9rem;
}

.footer-section ul {
    list-style: none;
    padding: 0;
    margin: 0 0 1rem;
}

.footer-section ul li {
    margin-bottom: 0.5rem;
}

.footer-section a {
    color: #00aaff;
    text-decoration: none;
    font-size: 0.9rem;
}

.footer-section a:hover {
    text-decoration: underline;
}

.download-link {
    display: inline-block;
    margin-top: 1rem;
    color: #fff;
    background-color: #ffffff;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    font-size: 0.9rem;
    text-transform: uppercase;
}

.download-link:hover {
    background-color: #6e30d8;
}

/* Follow Us Section */
.follow-us {
    margin-top: 20px;
}

.social-icons {
    margin-top: 10px;
}

.social-icons a {
    margin: 0 10px;
    color: #fff;
    font-size: 1.5rem;
    text-decoration: none;
}

.social-icons a:hover {
    color: #00aaff;
}

/* Contact Us Section */
.contact-us p {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

/* Footer Bottom */
.footer-bottom {
    text-align: center;
    margin-top: 20px;
    font-size: 0.8rem;
    border-top: 1px solid #444;
    padding-top: 10px;
}
